/* site/src/components/StudentPerformance.css */

/* Container styling */
.student-perf {
  background: #fff;
  padding: 2rem;
  margin: 1rem 0;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  color: #333;
}

/* Headings */
.student-perf h2,
.student-perf h3,
.student-perf h4 {
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

/* Tables */
.student-perf table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1.5rem;
}
.student-perf th,
.student-perf td {
  border: 1px solid #ddd;
  padding: 0.5rem;
  text-align: left;
}
.student-perf thead {
  background: #f5f5f5;
}
.student-perf tbody tr:nth-child(odd) {
  background: #fafafa;
}

/* Loading text */
.student-perf .loading {
  font-style: italic;
  color: #666;
}
